  --silver:#b4b8bc;
  --silver-lighter:#424242;
  --bronze:#caa789;
  --bronze-lighter:#4d433b;
  --fc-dark:var(--black-900);
  --fc-medium:var(--black-700);
  --fc-light:var(--black-500);
  --focus-ring:rgba(55, 138, 211, 0.15);
  --focus-ring-success:rgba(72, 116, 85, 0.4);
  --focus-ring-warning:rgba(158, 136, 59, 0.4);
  --focus-ring-error:rgba(196, 82, 79, 0.3);
  --focus-ring-muted:rgba(231, 232, 235, 0.1);
  --bs-sm:0 1px 2px rgba(0, 0, 0, 0.1), 0 1px 4px rgba(0, 0, 0, 0.1), 0 2px 8px rgba(0, 0, 0, 0.1);
  --bs-md:0 1px 3px rgba(0, 0, 0, 0.11), 0 2px 6px rgba(0, 0, 0, 0.11), 0 3px 8px rgba(0, 0, 0, 0.14);
  --bs-lg:0 1px 4px rgba(0, 0, 0, 0.14), 0 3px 8px rgba(0, 0, 0, 0.14), 0 4px 13px rgba(0, 0, 0, 0.18);
  --scrollbar:rgba(255, 255, 255, 0.2);
  background: var(--black) !important;
}

//same as --black
$primary-color: #263238;

header {
  .top_nav {
    li {
      .search-form {
        .form-inline {
          input.form-control {
            background-color: var(--black-050);
            color: var(--white);
            border: 0.5px solid var(--yellow-400);
          }
        }
      }
    }
  }

  .top-middle {
    background: rgba($primary-color, 0.7) !important;
    border-top-color: $primary-color;
    .title-desc {
      color: var(--yellow-400);
    }

    .title, .text-color-2, .text2, .title, .text-primary {
      text-shadow: none !important;
      color: var(--yellow-900) !important;
    }
  }

  .top-bottom {
    .navbar {
      background: var(--black-050) !important;
      .navbar-nav {
        li {
          border-top-color: var(--black-050);
          .dropdown-menu{
            background: var(--black-050);
            li{
              &.active, &:hover, &.active a.dropdown-item:hover, &:hover a.dropdown-item:hover {
                background: var(--black) !important;
              }
            }
          }
          a {
            color: var(--black-700) !important;
          }
          &.active, &:hover, &.active a.dropdown-item:hover, &:hover a.dropdown-item:hover {
            background: var(--black);
          }
        }
      }
    }
    .mega-menu {
      border-top: 4px solid var(--yellow-400);
      border-bottom: 4px solid var(--yellow-400);
      background: var(--black-050);
      p {
        color: var(--black-900);
      }
    }
  }
}

.main-content .main-content-inside, .bg-white {
  background: var(--black) !important;
}

.ega-bg, .bg-primary {
  background: var(--black-050) !important;
}

.container-fluid, .page-content p, .ega-meeting, .section-header, .text-dark, .news-title, .news-content, .container, .ega-footer-widget, .ega-contact, .modal-content, .bg-white {
  color: var(--white) !important;
}

a {
  color: var(--black-700) !important;

  &:hover{
    .text-dark {
      color: var(--yellow-800) !important;
    }
  }
}

.gradient-primary, .bg-light, .fixed .navbar-collapse, .card {
  background: var(--black-050) !important;
}

.text2 {
  &:after, &:before {
    background: var(--yellow-900);
  }
}

.navbar-light .navbar-nav .nav-link {
  &:focus, &:active, &:hover {
    color: var(--white-050) !important;
  }
}


.breadcrumb {
  background: var(--black) !important;

  .breadcrumb-item {
    color: var(--yellow-400) !important;
  }
}

.ega-svg {
  border-bottom: 100px solid rgba(38, 50, 56, 0.85) !important;

  .path-1, .path-2, .path-3 {
    fill: rgba(38, 50, 56, 0.35) !important;
  }
}

.path-core, .path-standard {
  fill: var(--black-050) !important;
}

.ega-services .ega-card-service ul li {
  a {
    background-color: var(--black) !important;

    &:before, &:after {
      background: var(--black-050) !important;
    }
  }

  span {
    color: var(--yellow-400);
  }
}

.nav-tabs {
  .nav-link{
    background-color: $primary-color;
    &:active, &.active{
      background-color: $primary-color;
    }
  }
}
.modal{
  .modal-body{
    background: var(--black-050);
  }
  .modal-footer{
    border-color: $primary-color;
    background: $primary-color;
  }
}
.b-15{
  border-color: var(--black-050);
}
table{
  background: var(--black-050);
}
.dataTables_wrapper {
  .dataTables_filter label input{
    background: $primary-color;
    color: white;
  }
  tbody{
    color: white;
  }
}
.post-review{
  background: var(--black-050) !important;
  .post-bar {
    li{
      color: white;
    }
  }
}
.home-page {

  .carousel{
    .mask{
      background: rgba($primary-color, 0.8);
    }
  }

  .zoom-card{
    &:hover{
      &.card{
        box-shadow: 0 0 15px $primary-color;
      }
    }
  }

  .section-title{
    color: var(--yellow-900);
  }
  .ega-news {
    .news-holder {
      background: var(--black) !important;
    }

    .news-title {
      color: var(--black-900);
    }

    .post-cont-info {
      border-color: 1px solid var(--yellow-900);
    }

    p {
      color: var(--black-700);
    }
  }
}

.video-testimonial-content {
  color: var(--black-700);
}

.ega-svg2 {
  background: var(--black) !important;

  .path-33 {
    fill: var(--black-050) !important;
  }

  .path-11 {
    fill: rgba(50, 66, 74, 0.3) !important;
  }

  .path-22 {
    fill: rgba(50, 66, 74, 0.6) !important;
  }
}

hr[class='bg-white'] {
  background-color: var(--yellow-900) !important;
}

.ega-heading-2 {
  color: var(--yellow-900);
}

.page-item.active .page-link {
  z-index: 1;
  color: var(--black-050);
  background-color: var(--yellow-900);
  border-color: var(--yellow-900);
}

.meeting-cover .meeting-nav .meeting-dots {
  background-color: var(--black);

  &:before, &:after {
    border-bottom: 40px solid var(--black);
  }

  li.active {
    border: 2px solid var(--yellow-900);
    color: var(--white);
  }
}

.modal-open .modal {
  background-color: rgba(38, 50, 56, 0.5);
}

.form-control {
  background-color: var(--black-050);
  color: var(--white);
  border: 0.5px solid var(--yellow-400);

  &:focus, &:active {
    background-color: var(--black-050);
    color: var(--white);
    border: 0.5px solid var(--yellow-400);
  }

  &:focus, &:active {}
}

.faq .card .card-header, .blog-form-bg {
  background: var(--black-050);
}

.faq .card-body, .unit .card-body {
  background: hsla(0, 13%, 25%, 0.32);
}

.video-wrapper .video-play-button {
  &:after, &:before {
    background: var(--black-050);
  }
}

.btn-outline-primary {
  color: var(--yellow-900) !important;
  border-color: var(--yellow-900) !important;

  &:not(:disabled):not(.disabled) {
    &.active, &:active {
      color: var(--black) !important;
      background-color: var(--yellow-900) !important;
      border-color: var(--yellow-900) !important;
    }
  }
}

.show > .btn-outline-primary.dropdown-toggle {
  color: var(--black) !important;
  background-color: var(--yellow-900) !important;
  border-color: var(--yellow-900) !important;
}

.btn-outline-primary {
  &:not(:disabled):not(.disabled):active, &:hover, &:active {
    color: var(--black) !important;
    background-color: var(--yellow-900) !important;
    border-color: var(--yellow-900) !important;
  }
}

footer{

  &.ega-footer {
    background-color: var(--black-050);

    .footer-content{
      .footer-heading{
        color: var(--yellow-900);
      }
    }
    .credit{
      background-color: var(--black);
    }

  }

}